body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center;margin-top:5%}.btn__followers{color:#525252;background-color:white;border:1px solid #858585;border-radius:3px;padding:4px 10px;cursor:pointer;transition:.3s linear}.btn__followers:hover{color:white;background-color:#858585}.btn__followers:focus{color:white;background-color:#858585}.cart__user-title{margin-top:20px}.cart__name{margin-bottom:10px;color:#5c5c5c}.cart__img{width:12%;margin-bottom:20px}.search__input{padding:5px 20px;border-radius:5px}.btn__search{margin-left:10px;border:none;background-color:white}.btn__search svg{vertical-align:top}

.btn__close-information{border:none;background-color:white;cursor:pointer}.btn__close-information svg{fill:grey}.followerSearchError{display:none}

.follower{display:flex;justify-content:center;flex-wrap:wrap}.img__follower{width:20%}.btn__followers{color:#525252;background-color:white;border:1px solid #858585;border-radius:3px;padding:4px 10px;cursor:pointer;transition:.3s linear}.btn__followers:hover{color:white;background-color:#858585}.btn__followers:focus{color:white;background-color:#858585}.follower__list{margin-bottom:3px}.closeFollowers{display:none}.closeInformationFollower{display:none}.btn__sort-name{border:none;color:#525252;background-color:white;border:1px solid #858585;border-radius:5px;cursor:pointer;transition:.3s linear}.btn__sort-name:hover{color:#525252;background-color:rgba(252,222,249,0.445)}.follower__information{display:flex}

.search{display:flex;flex-wrap:wrap;justify-content:center}.search__followers{margin-top:20px;margin-bottom:20px}

